.movie {
  width: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  .movie__intro {
    width: 80%;
    .movie__backdrop {
      width: 100%;
      height: 500px;
      object-fit: cover;
      object-position: 0 35%;
    }
  }
}

.movie__detail {
  align-items: center;
  width: 75%;
  display: flex;
  position: relative;
  bottom: 225px;
  .movie__detailLeft {
    margin-right: 30px;
    .movie__poster {
      width: 300px;
      border-radius: 10px;
      box-shadow: rgba(0, 0, 0, 0.86) 0px 22px 40px 6px;
    }
  }

  .movie__detailRight {
    color: white;
    display: flex;
    flex-direction: column;
    height: 450px;
    justify-content: space-between;
    .movie__detailRightTop {
      div {
        text-shadow: 0px 0px 20px #000000;
        margin-bottom: 0.5rem;
      }
      .movie__name {
        font-weight: 600;
        font-size: 3rem;
      }

      .movie__voteCount {
        margin-left: 1rem;
      }

      .movie__genres {
        margin: 1.25rem 0;
      }

      .movie__genre {
        padding: 0.5rem;
        border: 2px solid white;
        border-radius: 20px;
        margin-right: 1rem;
      }
    }
  }
  .movie__detailRightBottom {
    margin: 2rem 0;
    flex: 0.8;
    .synopsisText {
      font-size: 1.5rem;
      margin-bottom: 1.25rem;
      font-weight: 600;
      display: flex;
      position: relative;
      align-items: center;
    }
    .synopsisText > div:last-of-type {
      margin-left: auto;
    }
  }
}

.movie__heading {
  font-size: 2.2rem;
}

.movie__Button {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.8rem 2rem;
  border-radius: 20px;
  cursor: pointer;
  width: 150px;
  color: black;
  font-weight: bold;
}

.movie__production {
  width: 85%;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  flex-wrap: wrap;
  margin-bottom: 4rem;
}

.movie__productionComapany {
  width: 4rem;
  margin: 2rem;
  background-color: white;
  padding: 1rem;
}

.productionCompanyImage {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
